<template>
  <el-dialog
    :visible="visible"
    @close="() => { showResFlag = true;$emit('update:visible', false) }"
    width="80%"
    class="c-Result"
    :append-to-body="true"
  >
    <div
      v-for="(item, index) in result"
      :key="index"
      class="listrow"
      @click="
        event => {
          showRes(event, item);
        }
      "
    >
      <span class="name">
        原积分：{{ item.score }}积分
      </span>
      <span class="name">
        奖品：{{ item.name }}
      </span>
      <span v-if="item.passIn" class="name">
        流拍
      </span>
      <template v-else>
        <span class="name">
          成交人：{{ item.paimai_er }}
        </span>
        <span class="name">
          实际成交：{{ item.paimai_score }}积分
        </span>
      </template>
    </div>
  </el-dialog>
</template>
<script>
import { conversionCategoryName, getDomData } from '@/helper/index';
export default {
  name: 'c-Result',
  props: {
    visible: Boolean
  },
  computed: {
    result: {
      get() {
        console.log(this.$store.state.result)
        return this.$store.state.result.firstPrize;
      },
      set(val) {
        this.$store.commit('setResult', val);
      }
    },
  },
  data() {
    return {
      showResFlag: false
    }
  },
  methods: {
    showRes() {
      this.showResFlag = true
    },
  }
};
</script>
<style lang="scss">
.c-Result {
  .el-dialog__body {
    max-height: 500px;
    overflow-y: auto;
  }
  .listrow {
    display: flex;
    line-height: 30px;
    .name {
      font-weight: bold;
    }
    .name + .name {
      margin-left: 20px;
    }
    .value {
      flex: 1;
    }
    .card {
      display: inline-block;
      // width: 40px;
      padding: 0 5px;
      line-height: 30px;
      text-align: center;
      font-size: 18px;
      font-weight: bold;
      border-radius: 4px;
      border: 1px solid #ccc;
      background-color: #f2f2f2;
      margin-left: 5px;
      margin-bottom: 5px;
      position: relative;
      cursor: pointer;
      &:hover {
        &::before {
          content: '删除';
          width: 100%;
          height: 100%;
          background-color: #ccc;
          position: absolute;
          left: 0;
          top: 0;
          color: red;
        }
      }
    }
  }
}
</style>
