<template>
  <div id="root">
    <header>
      <Publicity v-show="!running" />
      <el-button class="res" type="text" @click="showResult = true">
        抽奖结果
      </el-button>
      <el-button class="con" type="text" @click="drawConfig">
        抽奖配置
      </el-button>
      <el-button class="third" type="text" @click="resetConfig">
        重置
      </el-button>
    </header>
    <div
      :style="`opacity: ${datas.length ? 1 : 0}`"
      id="main"
      :class="{ mask: showRes }"
    ></div>
    <div id="tags">
      <ul v-for="(item, i) in datas" :key="item.key">
        <li>
          <a
            href="javascript:void(0);"
            :style="{
              color: '#fff',
            }"
          >
            {{ item.name }}
          </a>
        </li>
      </ul>
    </div>
    <transition name="bounce">
      <div id="resbox" v-show="showRes">
        <div class="container">
          <span
            v-for="item in resArr"
            :key="item"
            class="itemres"
            :data-id="item"
          >
            <span class="cont">
              <div
                :style="{
                  fontSize: '23px',
                }"
              >
                {{ list.find((d) => d.key === item.key).score }}积分
              </div>
              <div style="font-size: 25px; color: red" v-if="showPrize">
                {{ list.find((d) => d.key === item.key).name }}
              </div>
              <div style="margin-top: 20px">
                <el-button
                  class="third"
                  type="primary"
                  size="mini"
                  @click="showPrize = true"
                >
                  查看奖品
                </el-button>
                <el-button
                  class="third"
                  type="success"
                  size="mini"
                  @click="dialogFormVisible = true"
                >
                  成交
                </el-button>
                <el-button
                  class="third"
                  type="danger"
                  size="mini"
                  @click="unConfirmPrize"
                >
                  流拍
                </el-button>
              </div>
            </span>
          </span>
        </div>
      </div>
    </transition>
    <el-dialog
      title="成交信息"
      class="paimai_form"
      :visible.sync="dialogFormVisible"
    >
      <el-form :inline="true" size="mini">
        <el-form-item label="成交人">
          <el-input v-model="paimai_er" placeholder="成交人"></el-input>
        </el-form-item>
        <el-form-item label="成交积分">
          <el-input v-model="paimai_score" placeholder="成交积分"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmPrize">确 定</el-button>
      </div>
    </el-dialog>

    <el-button
      class="audio"
      type="text"
      @click="
        () => {
          playAudio(!audioPlaying);
        }
      "
    >
      <i
        class="iconfont"
        :class="[audioPlaying ? 'el-icon-video-pause' : 'el-icon-video-play']"
      ></i>
    </el-button>
    <Result :visible.sync="showResult"></Result>

    <el-dialog
      :append-to-body="true"
      :visible.sync="showConfig"
      class="import-dialog"
      width="400px"
    >
      <el-input
        type="textarea"
        :rows="10"
        placeholder="请输入对应的积分和奖品，格式(积分 奖品)"
        v-model="listStr"
      ></el-input>
      <div class="footer">
        <el-button size="mini" type="primary" @click="transformList"
          >确定</el-button
        >
        <el-button size="mini" @click="showConfig = false">取消</el-button>
      </div>
    </el-dialog>

    <audio
      id="audiobg"
      preload="auto"
      controls
      autoplay
      loop
      @play="playHandler"
      @pause="pauseHandler"
    >
      <source :src="audioSrc" />
      你的浏览器不支持audio标签
    </audio>
    <el-image
      class="start_btn"
      :src="require('./assets/start.gif')"
      @click="toggle"
    ></el-image>
    <!-- <el-button class="start_btn" @click="toggle" type="primary" size="mini">{{
      running ? '停止' : '开始'
    }}</el-button> -->
    <div class="listrow">
      <div
        v-for="(item, index) in result.firstPrize"
        :key="index"
        @click="
          (event) => {
            showRes(event, item);
          }
        "
      >
        <span class="name"> 原积分：{{ item.score }}积分 </span>
        <span class="name"> 奖品：{{ item.name }} </span>
        <span v-if="item.passIn" class="name"> 流拍 </span>
        <template v-else>
          <span class="name"> 成交人：{{ item.paimai_er }} </span>
          <span class="name"> 实际成交：{{ item.paimai_score }}积分 </span>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import Publicity from "@/components/Publicity";
import bgaudio from "@/assets/bg.mp3";
import beginaudio from "@/assets/begin.mp3";
import {
  getData,
  configField,
  resultField,
  conversionCategoryName,
  listField,
} from "@/helper/index";
import { randomNum } from "@/helper/algorithm";
import Result from "@/components/Result";
import { database, DB_STORE_NAME } from "@/helper/db";
export default {
  name: "App",

  components: { Publicity, Result },

  computed: {
    list: {
      get() {
        return this.$store.state.list;
      },
    },
    result: {
      get() {
        return this.$store.state.result;
      },
      set(val) {
        this.$store.commit("setResult", val);
      },
    },
    list() {
      return this.$store.state.list;
    },
    allresult() {
      let allresult = [];
      for (const key in this.result) {
        if (this.result.hasOwnProperty(key)) {
          const element = this.result[key];
          allresult = allresult.concat(element);
        }
      }
      return allresult;
    },
    datas() {
      console.log(this.list);
      return this.list.filter((item) => !item.status) || [];
    },
    categoryName() {
      return conversionCategoryName(this.category);
    },
    photos() {
      return this.$store.state.photos;
    },
  },
  created() {
    const data = getData(configField);
    if (data) {
      this.$store.commit("setConfig", Object.assign({}, data));
    }
    const result = getData(resultField);
    if (result) {
      this.$store.commit("setResult", result);
    }
    const list = getData(listField);
    if (list) {
      this.$store.commit("setList", list);
    }
  },

  data() {
    return {
      running: false,
      showRes: false,
      showConfig: false,
      showResult: false,
      showPrize: false,
      dialogFormVisible: false,
      paimai_er: "",
      paimai_score: "",
      resArr: [],
      category: "",
      audioPlaying: false,
      audioSrc: bgaudio,
      listStr: "",
    };
  },
  watch: {
    photos: {
      deep: true,
      handler() {
        this.$nextTick(() => {
          this.reloadTagCanvas();
        });
      },
    },
  },
  mounted() {
    this.startTagCanvas();
    setTimeout(() => {
      this.getPhoto();
    }, 1000);
    window.addEventListener("resize", this.reportWindowSize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.reportWindowSize);
  },
  methods: {
    drawConfig() {
      this.listStr = this.list
        .filter((item) => !item.status)
        .map((item) => `${item.score} ${item.name}`)
        .join("\n");
      this.showConfig = true;
    },
    resetConfig() {
      this.$confirm("此操作将重置抽奖结果数据，是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$store.commit("setClearStore");
          database.clear(DB_STORE_NAME);

          this.closeRes && this.closeRes();

          this.list.forEach((item) => (item.status = false));
          this.$store.commit("setList", this.list);

          this.showRemoveoptions = false;
          this.$message({
            type: "success",
            message: "重置成功!",
          });

          this.$nextTick(() => {
            this.reloadTagCanvas();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    transformList() {
      const { listStr } = this;
      if (!listStr) {
        this.$message.error("没有数据");
      }
      const list = [];
      const rows = listStr.split("\n");
      if (rows && rows.length > 0) {
        rows.forEach((item, i) => {
          const rowList = item.split(/\t|\s/);
          if (rowList.length >= 2) {
            const score = Number(rowList[0].trim());
            const name = rowList[1].trim();
            score &&
              list.push({
                key: i + 1,
                score,
                name,
                status: false,
              });
          }
        });
      }
      this.$store.commit("setList", list);

      this.$message({
        message: "保存成功",
        type: "success",
      });
      this.showConfig = false;
      this.$nextTick(() => {
        this.reloadTagCanvas();
      });
    },
    reportWindowSize() {
      const AppCanvas = this.$el.querySelector("#rootcanvas");
      if (AppCanvas.parentElement) {
        AppCanvas.parentElement.removeChild(AppCanvas);
      }
      this.startTagCanvas();
    },
    playHandler() {
      this.audioPlaying = true;
    },
    pauseHandler() {
      this.audioPlaying = false;
    },
    playAudio(type) {
      if (type) {
        this.$el.querySelector("#audiobg").play();
      } else {
        this.$el.querySelector("#audiobg").pause();
      }
    },
    loadAudio() {
      this.$el.querySelector("#audiobg").load();
      this.$nextTick(() => {
        this.$el.querySelector("#audiobg").play();
      });
    },
    getPhoto() {
      database.getAll(DB_STORE_NAME).then((res) => {
        if (res && res.length > 0) {
          this.$store.commit("setPhotos", res);
        }
      });
    },
    speed() {
      return [0.1 * Math.random() + 0.01, -(0.1 * Math.random() + 0.01)];
    },
    createCanvas() {
      const canvas = document.createElement("canvas");
      canvas.width = document.body.offsetWidth;
      canvas.height = document.body.offsetHeight;
      canvas.id = "rootcanvas";
      this.$el.querySelector("#main").appendChild(canvas);
    },
    startTagCanvas() {
      this.createCanvas();
      const { speed } = this;
      window.TagCanvas.Start("rootcanvas", "tags", {
        textColour: null,
        initial: speed(),
        dragControl: 1,
        textHeight: 20,
        noSelect: true,
        lock: "xy",
      });
    },
    reloadTagCanvas() {
      window.TagCanvas.Reload("rootcanvas");
    },
    closeRes() {
      this.showRes = false;
    },
    unConfirmPrize() {
      this.$store.commit("setResult", {
        firstPrize: this.result.firstPrize.concat({
          ...this.resArr[0],
          passIn: true,
        }),
      });
      this.paimai_er = "";
      this.paimai_score = "";
      this.dialogFormVisible = false;
      this.showRes = false;
      this.showPrize = false;
    },
    confirmPrize() {
      this.$store.commit("setResult", {
        firstPrize: this.result.firstPrize.concat({
          ...this.resArr[0],
          paimai_er: this.paimai_er,
          paimai_score: this.paimai_score,
        }),
      });
      this.paimai_er = "";
      this.paimai_score = "";
      this.dialogFormVisible = false;
      this.showRes = false;
      this.showPrize = false;
    },
    toggle() {
      if (!this.datas.length) {
        this.$message({
          type: "warning",
          message: "没有奖品了!",
        });
        return;
      }
      const { speed } = this;
      if (!this.running) {
        this.showRes = false;

        this.audioSrc = beginaudio;
        this.loadAudio();
        let len = this.datas.length;
        let resIdx = randomNum(1, len) - 1;
        let res = this.datas[resIdx];
        res.status = true;
        this.resArr = [res];
        this.$store.commit("setList", this.list);
        window.TagCanvas.SetSpeed("rootcanvas", [5, 1]);
        this.running = !this.running;
        setTimeout(() => {
          this.audioSrc = bgaudio;
          this.loadAudio();
          window.TagCanvas.SetSpeed("rootcanvas", speed());
          this.showRes = true;
          this.running = !this.running;
          this.$nextTick(() => {
            this.reloadTagCanvas();
          });
        }, 1500);
      }
    },
  },
};
</script>
